<template>
  <v-container
    fluid
    grid-list-md>
    <v-form
      ref="form2"
      @submit.prevent="validateBeforeSubmit">
      <v-layout
        row
        wrap>
        <v-flex md12>
          <v-card class="elevation-0">
            <v-card-title class="headline">
              Fila de Cobrança
            </v-card-title>
            <v-card-text>
              <v-layout
                row
                wrap>
                <v-flex md12>
                  <core-filtro-rede-empresa-loja
                    v-model="filtros"
                    :show-todas="true"
                    :obrigatorio="false"
                  />
                </v-flex>
                <v-flex
                  md2
                  sm6
                  xs12
                >
                  <v-text-field
                    v-model="filtros.atrasoDe"
                    label="Atraso De"
                    type="number"
                    reverse
                  />
                </v-flex>
                <v-flex
                  md2
                  sm6
                  xs12
                >
                  <v-text-field
                    v-model="filtros.atrasoAte"
                    label="Atraso Até"
                    type="number"
                    reverse
                  />
                </v-flex>
                <v-flex
                  md2
                  sm6
                  xs12
                >
                  <v-text-field
                    v-formata-moeda="filtros.saldoDevedor"
                    v-money="money"
                    v-model="filtros.saldoDevedor"
                    label="Saldo Devedor Até"
                    type="tel"
                    suffix="R$"
                    reverse
                    hint="0,00 é todas as faixas"
                  />
                </v-flex>
                <v-flex
                  md2
                  sm6
                  xs12
                >
                  <v-autocomplete
                    v-model="filtros.clienteEmAcordo"
                    :items="clientesAcordo"
                    label="Clientes Acordo"
                    item-value="id"
                    item-text="descricao"
                  />
                </v-flex>
                <v-flex
                  md2
                  sm6
                  xs12
                >
                  <v-autocomplete
                    v-model="filtros.fatura"
                    :items="faturas"
                    label="Prim. Fatura"
                    item-value="id"
                    item-text="descricao"
                  />
                </v-flex>
                <v-flex
                  md2
                  sm6
                  xs12
                >
                  <v-autocomplete
                    v-model="filtros.atrasoMes"
                    :items="atrasoMes"
                    label="Atraso Mês"
                    item-value="id"
                    item-text="descricao"
                  />
                </v-flex>
                <v-flex md12>
                  <v-checkbox
                    v-model="filtros.incluirClienteSPC"
                    label="Incluir os clientes que estão SERASA/SPC"
                  />
                </v-flex>
                <v-flex md12>
                  <v-checkbox
                    v-model="filtros.incluirAgendamento"
                    label="Incluir agendamentos (lista primeiro os agendamentos do usuário independente do filtro escolhido)"
                  />
                </v-flex>
                <v-flex md12>
                  <v-layout
                    row
                    class="layout align-center ml-1">
                    <v-label>
                      Colocar na fila de cobrança apenas os clientes sem pagamento ou com último pagamento até
                    </v-label>
                    <v-text-field
                      v-model="filtros.ultimoPagamento"
                      type="date"
                      class="text-field-custom ml-2"
                    />
                  </v-layout>
                </v-flex>
              </v-layout>
            </v-card-text>
          </v-card>
        </v-flex>
        <v-flex md12>
          <v-layout class="justify-end">
            <v-btn
              :loading="loadingBtn"
              type="submit"
              color="primary">Iniciar Cobrança</v-btn>
          </v-layout>
        </v-flex>

        <core-progress-modal :show="loading" />
      </v-layout>
    </v-form>
  </v-container>
</template>

<script>
import { CobrancaBusiness } from '../../business'
import { TODAS_ID, MONEY } from '../../constants'

export default {
  props: {
    // eslint-disable-next-line vue/require-default-prop
    clientesAcordo: {
      type: Array,
      requided: true
    },
    // eslint-disable-next-line vue/require-default-prop
    faturas: {
      type: Array,
      requided: true
    },
    // eslint-disable-next-line vue/require-default-prop
    atrasoMes: {
      type: Array,
      requided: true
    }
  },
  data () {
    return {
      money: MONEY,
      filtros: {
        redeId: TODAS_ID,
        empresaId: TODAS_ID,
        lojaId: TODAS_ID,
        atrasoDe: null,
        atrasoAte: null,
        incluirAgendamento: true,
        clienteEmAcordo: '',
        incluirClienteSPC: false,
        fatura: '',
        atrasoMes: TODAS_ID
      },
      loadingBtn: false,
      loading: false
    }
  },
  beforeMount () {

  },
  methods: {
    validateBeforeSubmit () {
      this.$validator.validate().then(result => {
        if (result) {
          this.loading = true
          this.loadingBtn = true
          CobrancaBusiness.iniciarCobranca(this.filtros)
            .then(response => {
              this.$root.addFilaCobranca(response.data)
              this.proximoCliente()
            })
            .catch(erro => this.$root.showErro(erro.data))
            .finally(() => {
              this.loading = false
              this.loadingBtn = false
            })
        } else {
          this.$root.showAlerta(this.$vuetify.t('$vuetify.Erro.camposObrigatorios'))
        }
      })
    },
    proximoCliente () {
      let clienteFila = this.$root.getProximoFilaCobranca()
      if (clienteFila) {
        this.$router.push('/cliente-cobranca?payload=' + JSON.stringify(clienteFila))
      } else {
        this.$root.showAlerta('Não encontrou clientes para cobrar, ajustes os filtros informados e tente novamente.')
      }
    }
  }
}
</script>
